  function getFeatureStyle1(feature) {
    let p_grade = feature.get('p_grade');
    let p_arate1 = feature.get('p_arate');
    let color = 'rgba(145, 145, 145, 0.6)'; // 
  
    if (p_arate1 !=null && p_grade=='' ) {
      color = 'rgba(0, 255, 0, 0.6)'; // 
    } else if (p_arate1 !=null && p_grade== '') {
      color = 'rgba(255, 255, 0, 0.6)'; // 
    } else if (p_arate1 !=null && p_grade== '') {
      color = 'rgba(255, 165, 0, 0.6)'; // 
    } else if (p_arate1 !=null && p_grade=='') {
      color = 'rgba(255, 0, 0, 0.6)'; // 
    }else if (p_arate1 !=null && p_grade=='') {
      color = 'rgba(0, 0, 255, 0.6)'; // 
    }
  
    return new Style({
      stroke: new Stroke({
        color: 'rgba(255, 255, 255, 0.4)',
        width: 1
      }),
      fill: new Fill({
        color: color
      })
    });
  }

