function getCQLfilter() {
  let sCQL = "";

  //   //       (  )
  const exclude01 = document.getElementById("exclude01");
  const exclude02 = document.getElementById("exclude02");
  const exclude03 = document.getElementById("exclude03");
  const exclude04 = document.getElementById("exclude04");

  const grade01 = document.getElementById("grade01");
  const grade02 = document.getElementById("grade02");
  const grade03 = document.getElementById("grade03");
  const grade04 = document.getElementById("grade04");
  const grade05 = document.getElementById("grade05");

  // filter filtersearch 
  //    ( .
  if ((true == exclude01.checked) || (true == exclude02.checked) || (true == exclude03.checked) || (true == exclude04.checked))

    sCQL += "(";//}

  if (true == exclude01.checked) {
    sCQL = sCQL + "not youngdo in ('1','1','2','3','')"
  }

  if (true == exclude02.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " and "
    sCQL = sCQL + "not youngdo = ''"
  }

  if (true == exclude03.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " and "
    sCQL = sCQL + "not youngdo in ('','','')"
  }

  if (true == exclude04.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " and "
    sCQL = sCQL + "not youngdo in ('1','1','2','3','','','','','')"
  }

  //     ) 
  if ((true == exclude01.checked) || (true == exclude02.checked) || (true == exclude03.checked) || (true == exclude04.checked))
    if (0 < sCQL.length)
      sCQL += ")";


  if ((true == grade01.checked) || (true == grade02.checked) || (true == grade03.checked) || (true == grade04.checked) || (true == grade05.checked)) {
    if (0 < sCQL.length)
      sCQL += " and "
    sCQL += "(";
  }

  if (true == grade01.checked) {
    sCQL += "grade = ''"
  }

  if (true == grade02.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " or "
    sCQL += "grade=''"
  }

  if (true == grade03.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " or "
    sCQL += "grade=''"
  }

  if (true == grade04.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " or "
    sCQL += "grade=''"
  }

  if (true == grade05.checked) {
    if (sCQL.charAt(sCQL.length - 1) != '(')
      sCQL += " or "
    sCQL += "grade=''"
  }

  if ((true == grade01.checked) || (true == grade02.checked) || (true == grade03.checked) || (true == grade04.checked) || (true == grade05.checked))
    sCQL += ")"
  console.log("filter=" + sCQL)
  return sCQL;
}

function getFeatureStyle(feature) {
  const grade = feature.get('grade');
  const grade01 = document.getElementById("grade01").checked;
  const grade02 = document.getElementById("grade02").checked;
  const grade03 = document.getElementById("grade03").checked;
  const grade04 = document.getElementById("grade04").checked;
  const grade05 = document.getElementById("grade05").checked;

  let color = 'rgba(75,137,220, 0)'; //  

  if (grade01 && (grade === '')) {
    color = 'rgba(0, 255, 0, 0.6)'; // 
  } else if (grade02 && grade === '') {
    color = 'rgba(255, 255, 0, 0.6)'; // 
  } else if (grade03 && grade === '') {
    color = 'rgba(255, 165, 0, 0.6)'; // 
  } else if (grade04 && grade === '') {
    color = 'rgba(255, 0, 0, 0.6)'; // 
  }else if (grade05 && grade === '') {
    color = 'rgba(0, 0, 255, 0.6)'; // 
  }

  return new Style({
    stroke: new Stroke({
      color: 'rgba(75,137,220, 0.4)',
      width: 1
    }),
    fill: new Fill({
      color: color
    })
  });
}

